@font-face {
  font-family: Hanken Grotesk;
  src: url("HankenGrotesk-VariableFont_wght.4d912b22.ttf");
}

.container {
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.results {
  flex-direction: column;
  font-family: Hanken Grotesk, sans-serif;
  font-weight: 500;
  display: flex;
  box-shadow: 0 14px 18px 13px #ebf1ff;
}

.results__result {
  text-align: center;
  background: linear-gradient(#7857ff, #2e2be9);
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 60px 40px;
  display: flex;
}

.results__title {
  color: #c8c7ff;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 700;
}

.results__score {
  background: linear-gradient(to top, #2421ca00, #4e21ca);
  border-radius: 999px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 20px 40px;
}

.results__value {
  color: #ebf1ff;
  font-size: 3rem;
  font-weight: 800;
}

.results__total {
  color: #c8c7ff;
  font-size: .875rem;
}

.results__description {
  color: #ebf1ff;
  margin-top: 10px;
}

.results__subtitle {
  font-size: 1.5rem;
}

.results__text {
  color: #c8c7ff;
  margin-top: 5px;
  font-size: .875rem;
}

.results__summary {
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
}

.results__items {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.results__button {
  cursor: pointer;
  border: none;
  border-radius: 30px;
  padding-top: 13px;
  padding-bottom: 13px;
}

.results__button--bg-primary {
  color: #fff;
  background-color: #303b5a;
}

.results__button--bg-primary:hover {
  background: linear-gradient(#7857ff, #2e2be9);
}

.results__item {
  border-radius: 8px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 15px;
  padding: 15px;
  font-size: .875rem;
  display: flex;
}

.results__item--bg-red {
  background-color: #ff57571a;
}

.results__item--bg-yellow {
  background-color: #ffb01f1a;
}

.results__item--bg-green {
  background-color: #00bd911a;
}

.results__item--bg-blue {
  background-color: #1125d41a;
}

.item__block {
  flex-direction: row;
  display: flex;
}

.item__icon {
  width: 27px;
}

.item__title {
  margin-left: 10px;
  font-weight: 700;
}

.item__title--color-red {
  color: #ff5757;
}

.item__title--color-yellow {
  color: #ffb01f;
}

.item__title--color-green {
  color: #00bd91;
}

.item__title--color-blue {
  color: #1125d4;
}

.item__ratingtotal {
  color: #98989a;
}

@media screen and (width >= 1023px) {
  .results {
    border-radius: 30px;
    flex-direction: row;
    max-width: 41.5rem;
  }

  .results__result {
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    padding-top: 0;
    padding-left: 50px;
    padding-right: 50px;
  }

  .results__title {
    font-size: 1.25rem;
  }

  .results__score {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 30px 50px;
  }

  .results__value {
    font-size: 4.5rem;
  }

  .results__total {
    margin-top: -15px;
    font-size: 1rem;
  }

  .results__subtitle {
    font-size: 1.75rem;
  }

  .results__text {
    margin-top: 10px;
    font-size: 1rem;
  }

  .results__summary {
    margin-left: 8px;
  }

  .results__summary .results__title {
    margin-bottom: 20px;
    font-size: 1rem;
  }

  .results__button {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

